"use client"

import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card"
import { Button } from "@/components/ui/button"
import { useEffect, useState } from "react"
import Link from "next/link"

export default function CarDashboard() {
  const [pollutionRate, setPollutionRate] = useState(45)
  const [isRpmMode, setIsRpmMode] = useState(false)
  const [currentRpm, setCurrentRpm] = useState(4500)
  const [alertLevel, setAlertLevel] = useState("")

  // Car details
  const carDetails = {
    carNumber: "MH-12-AB-1234",
    carModel: "Toyota Camry Hybrid 2024",
    ownerName: "Rajesh Kumar Sharma"
  }

  useEffect(() => {
    // Simulate real-time pollution data
    const interval = setInterval(() => {
      setPollutionRate(prev => {
        const change = (Math.random() - 0.5) * 5
        const newRate = Math.max(0, Math.min(100, prev + change))
        
        // Set alert levels
        if (newRate >= 70) {
          setAlertLevel("CRITICAL 70%")
        } else if (newRate >= 60) {
          setAlertLevel("WARNING 60%")
        } else if (newRate >= 50) {
          setAlertLevel("ALERT 50%")
        } else {
          setAlertLevel("")
        }

        // RPM mode control
        if (newRate > 75) {
          setIsRpmMode(true)
          setCurrentRpm(3000) // Limited RPM
        } else {
          setIsRpmMode(false)
          setCurrentRpm(4500) // Normal RPM
        }

        return newRate
      })
    }, 2000)

    return () => clearInterval(interval)
  }, [])

  const getPollutionColor = () => {
    if (pollutionRate < 50) return "text-green-400"
    if (pollutionRate < 60) return "text-yellow-400"
    if (pollutionRate < 70) return "text-orange-400"
    return "text-red-400"
  }

  const getStatusLight = () => {
    if (pollutionRate < 50) {
      return "bg-green-500 shadow-green-500/50"
    }
    return "bg-red-500 shadow-red-500/50 animate-pulse"
  }

  return (
    <div className="min-h-screen bg-gradient-to-br from-slate-900 via-gray-900 to-black text-white p-6">
      <div className="container mx-auto max-w-7xl">
        {/* Header */}
        <div className="flex justify-between items-center mb-8">
          <div>
            <h1 className="text-4xl font-bold bg-gradient-to-r from-green-400 to-blue-500 bg-clip-text text-transparent">
              Car Dashboard
            </h1>
            <p className="text-gray-400 mt-2">Real-time pollution monitoring and engine control</p>
          </div>
          <Link href="/">
            <Button variant="outline" className="border-gray-600 text-gray-300 hover:bg-gray-800">
              ‚Üê Back to Home
            </Button>
          </Link>
        </div>

        {/* Alert Banner */}
        {alertLevel && (
          <div className="mb-6 bg-red-900/30 border border-red-500/50 rounded-lg p-4 backdrop-blur-sm animate-pulse">
            <div className="flex items-center gap-3">
              <div className="w-4 h-4 bg-red-500 rounded-full animate-ping"></div>
              <span className="text-red-400 font-bold text-lg">üö® POLLUTION {alertLevel}</span>
            </div>
            <p className="text-red-300 mt-2">
              {isRpmMode 
                ? "Engine RPM has been limited to 3000 to reduce emissions!" 
                : "Take immediate action to reduce vehicle emissions!"
              }
            </p>
          </div>
        )}

        <div className="grid lg:grid-cols-3 gap-8">
          {/* Car Information */}
          <Card className="bg-black/40 border-blue-500/30 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-blue-400 flex items-center gap-2">
                <div className="w-3 h-3 bg-blue-500 rounded-full"></div>
                Vehicle Information
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-4">
              <div>
                <label className="text-gray-400 text-sm">Car Number</label>
                <div className="text-white font-mono text-lg bg-gray-800/50 p-2 rounded">
                  {carDetails.carNumber}
                </div>
              </div>
              <div>
                <label className="text-gray-400 text-sm">Car Model</label>
                <div className="text-white font-semibold">
                  {carDetails.carModel}
                </div>
              </div>
              <div>
                <label className="text-gray-400 text-sm">Owner Name</label>
                <div className="text-white font-semibold">
                  {carDetails.ownerName}
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Pollution Monitor */}
          <Card className="bg-black/40 border-green-500/30 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-green-400 flex items-center gap-2">
                <div className={`w-4 h-4 rounded-full ${getStatusLight()}`}></div>
                Pollution Monitor
              </CardTitle>
            </CardHeader>
            <CardContent className="text-center">
              <div className="relative w-32 h-32 mx-auto mb-6">
                <div className={`w-full h-full rounded-full border-8 ${
                  pollutionRate < 50 ? 'border-green-500' : 'border-red-500'
                } flex items-center justify-center bg-gradient-to-br ${
                  pollutionRate < 50 ? 'from-green-400/20 to-green-600/20' : 'from-red-400/20 to-red-600/20'
                }`}>
                  <div className={`text-3xl font-bold ${getPollutionColor()}`}>
                    {Math.round(pollutionRate)}%
                  </div>
                </div>
                {pollutionRate >= 50 && (
                  <div className="absolute -top-2 -right-2 w-6 h-6 bg-red-500 rounded-full animate-ping"></div>
                )}
              </div>
              
              <div className="space-y-2">
                <div className={`text-lg font-semibold ${getPollutionColor()}`}>
                  {pollutionRate < 50 ? "CLEAN AIR" : alertLevel}
                </div>
                <div className="text-gray-400 text-sm">
                  Current pollution level
                </div>
              </div>

              {/* Status Lights */}
              <div className="flex justify-center gap-4 mt-6">
                <div className="text-center">
                  <div className={`w-8 h-8 rounded-full mx-auto mb-2 ${
                    pollutionRate < 50 ? 'bg-green-500 shadow-lg shadow-green-500/50' : 'bg-gray-600'
                  }`}></div>
                  <div className="text-xs text-gray-400">Clean</div>
                </div>
                <div className="text-center">
                  <div className={`w-8 h-8 rounded-full mx-auto mb-2 ${
                    pollutionRate >= 50 ? 'bg-red-500 shadow-lg shadow-red-500/50 animate-pulse' : 'bg-gray-600'
                  }`}></div>
                  <div className="text-xs text-gray-400">Alert</div>
                </div>
              </div>
            </CardContent>
          </Card>

          {/* Engine Control */}
          <Card className="bg-black/40 border-purple-500/30 backdrop-blur-sm">
            <CardHeader>
              <CardTitle className="text-purple-400 flex items-center gap-2">
                <div className={`w-3 h-3 rounded-full ${isRpmMode ? 'bg-red-500 animate-pulse' : 'bg-green-500'}`}></div>
                Engine Control
              </CardTitle>
            </CardHeader>
            <CardContent className="space-y-6">
              <div className="text-center">
                <div className={`text-4xl font-bold mb-2 ${isRpmMode ? 'text-red-400' : 'text-green-400'}`}>
                  {currentRpm}
                </div>
                <div className="text-gray-400 text-sm">Current RPM</div>
              </div>

              <div className={`p-4 rounded-lg border ${
                isRpmMode 
                  ? 'bg-red-900/20 border-red-500/30 text-red-300' 
                  : 'bg-green-900/20 border-green-500/30 text-green-300'
              }`}>
                <div className="font-semibold mb-1">
                  {isRpmMode ? "‚ö†Ô∏è RPM LIMITED MODE" : "‚úÖ NORMAL MODE"}
                </div>
                <div className="text-sm">
                  {isRpmMode 
                    ? "Engine speed reduced to limit emissions" 
                    : "Engine operating at normal parameters"
                  }
                </div>
              </div>

              <div className="space-y-3">
                <div className="flex justify-between">
                  <span className="text-gray-400">Max RPM:</span>
                  <span className={isRpmMode ? 'text-red-400' : 'text-green-400'}>
                    {isRpmMode ? '3000' : '6000'}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-400">Speed Limit:</span>
                  <span className={isRpmMode ? 'text-red-400' : 'text-green-400'}>
                    {isRpmMode ? 'Reduced' : 'Normal'}
                  </span>
                </div>
                <div className="flex justify-between">
                  <span className="text-gray-400">ECU Status:</span>
                  <span className="text-blue-400">Connected</span>
                </div>
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Pollution Simulator */}
        <Card className="mt-8 bg-black/40 border-gray-600/30 backdrop-blur-sm">
          <CardHeader>
            <CardTitle className="text-yellow-400">üîß Pollution Simulator (Testing)</CardTitle>
            <CardDescription className="text-gray-400">
              Adjust pollution levels to test system responses
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div>
                <label className="block text-sm text-gray-400 mb-2">
                  Pollution Level: {Math.round(pollutionRate)}%
                </label>
                <input
                  type="range"
                  min="0"
                  max="100"
                  value={pollutionRate}
                  onChange={(e) => setPollutionRate(Number(e.target.value))}
                  className="w-full h-2 bg-gray-700 rounded-lg appearance-none cursor-pointer slider"
                />
              </div>
              <div className="flex gap-2">
                <Button 
                  onClick={() => setPollutionRate(30)}
                  className="bg-green-600 hover:bg-green-700"
                  size="sm"
                >
                  Clean (30%)
                </Button>
                <Button 
                  onClick={() => setPollutionRate(55)}
                  className="bg-yellow-600 hover:bg-yellow-700"
                  size="sm"
                >
                  Alert (55%)
                </Button>
                <Button 
                  onClick={() => setPollutionRate(80)}
                  className="bg-red-600 hover:bg-red-700"
                  size="sm"
                >
                  Critical (80%)
                </Button>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    </div>
  )
}
